<template>
<div class="">
  <mt-tabbar v-model="selected" :fixed="true">
    <mt-tab-item id="赛事中心">
      <img slot="icon" :src="images.img1">
      赛事中心
    </mt-tab-item>
    <mt-tab-item id="教练中心">
      <img slot="icon" :src="images.img2">
      教练中心
    </mt-tab-item>
    <mt-tab-item id="场馆中心">
      <img slot="icon" :src="images.img3">
      场馆中心
    </mt-tab-item>
    <mt-tab-item id="个人中心">
      <img slot="icon" :src="images.img4">
      个人中心
    </mt-tab-item>
  </mt-tabbar>
</div>
</template>
<script>
export default {
  name: "Menu",
  data() {
    return {
      selected:this.$store.state.menuData,
      images:{
        img1:require("@/assets/images/notebook.png"),
        img2:require("@/assets/images/print.png"),
        img3:require("@/assets/images/setup.png"),
        img4:require("@/assets/images/test.png")
      }
    }
  },
  activated(){
    this.selected=this.$store.state.menuData
  },
  watch:{
    selected(val){
      this.$store.state.menuData=val
      console.log(val);
      if(val=="教练中心"){
        this.$router.push("/coach")
      }else if(val=="赛事中心"){
        this.$router.push("/main")
      }else if(val=="场馆中心"){
        this.$router.push("/venue")
      }
    }
  }
}
</script>
<style scoped>

</style>



