<template>
<div class="main">
  <div class="commen-head">热门赛事</div>
  <div class="hot-content">
    <div v-for="item in list" class="hot-item" @click="toDetail(item.id)">
      <div class="hot-pic">
        <img :src="item.src"/>
      </div>
      <div class="hot-txt">
        <h1 class="hot-title">{{item.hotTitle}}</h1>
        <div class="hot-desc">{{item.hotDesc}}</div>
      </div>
    </div>
  </div>
  <div class="hot-more">
    <a>查看更多</a>
  </div>
</div>
</template>
<script>
export default {
  name: "HotGame",
  data() {
    return {
      list:[
        {
          id:"1",
          src:"https://img.alicdn.com/tfs/TB1td1qB5rpK1RjSZFhXXXSdXXa-60-30.png",
          hotTitle:"你说你最近海马的地方打",
          hotDesc:"的地方打发放到发大水发大发发大水大幅度发"
        },
        {
          id:"2",
          src:"https://img.alicdn.com/tfs/TB1td1qB5rpK1RjSZFhXXXSdXXa-60-30.png",
          hotTitle:"2你说你最近海马的地方打",
          hotDesc:"的地方打发放到发大水发大发发大水大幅度发"
        },
        {
          id:"3",
          src:"https://img.alicdn.com/tfs/TB1td1qB5rpK1RjSZFhXXXSdXXa-60-30.png",
          hotTitle:"3你说你最近海马的地方打",
          hotDesc:"的地方打发放到发大水发大发发大水大幅度发"
        }
      ]
    }
  },
  methods:{
    toDetail(id){
      this.$router.push('/main/detail/'+id)
    }
  }
}
</script>
<style scoped="">
.main {
  padding: 10px;
}

.mint-header {
  background: none;
  color: #000;
}
</style>



