<template>
<div class="">
  <mt-header title="注册">
    <router-link to="/loginStudent" slot="left">
      <mt-button icon="back"></mt-button>
    </router-link>
  </mt-header>
  <div class="main">
    <div class="input-group">
      <mt-field placeholder="请输入手机号" type="tel" v-model="telephone"></mt-field>
    </div>
    <div class="input-group">
      <mt-field v-model="captcha" @click.native.capture="getCode" placeholder="请输入验证码">{{text}}
      </mt-field>
    </div>
    <div class="input-group">
      <mt-field placeholder="请输入密码" type="password" v-model="password"></mt-field>
    </div>
    <div class="input-group">
      <mt-field placeholder="请再次输入密码" type="password" v-model="repeatPwd"></mt-field>
    </div>
    <div class="button" @click="register()">注册</div>
  </div>
</div>
</template>
<script>
export default {
  name: "Register",
  data() {
    return {
      telephone: "",
      password: "",
      repeatPwd: "",
      captcha: "",
      text: "获取验证码",
      timer: null
    }
  },
  methods: {
    getCode() {
      if (this.timer == null) {
        this.codeTimer()
      }
    },
    codeTimer() {
      let seconds = 10
      this.text = "10秒后重新获取"
      this.timer = setInterval(() => {
        seconds--
        this.text = seconds + "秒后重新获取"
        if (seconds == 0) {
          clearInterval(this.timer)
          this.timer = null
          this.text = "点击重新获取"
        }
      }, 1000)
    },
    regisAjax() {
      this.axios.post(this.$store.state.domain+"api/user/register", {
          mobile: this.telephone,
          mobile_code: this.captcha,
          password:this.password,
          password_reply:this.repeatPwd
      }).then((data) => {
        console.log(data);
        let msg=data.data.msg
        console.log(msg)
        if(msg=="验证码错误"){
          this.$toast({
            message: '验证码错误！',
            position: 'bottom',
            iconClass:'',
            duration: 3000
          });
        }else if(msg=="登录成功"){
          let ac_token=data.data.data.ac_token
          localStorage.setItem("ac_token",ac_token);
          this.$toast({
            message: '注册成功,稍后跳转！',
            position: 'bottom',
            iconClass: '',
            duration: 3000
          });
          setTimeout(()=>{
            this.$router.push("/loginStudent")
          },2000)
        }
      })
    },
    submitTest(){   // 验证手机号
      const telReg=/^1[0-9]{10}/g // 手机号匹配
      const codeReg=/[0-9]{4}/g // 验证码匹配
      if(!telReg.test(this.telephone)){  // 手机号验证
        this.$toast({
          message: '手机号格式错误！',
          position: 'bottom',
          iconClass:'',
          duration: 3000
        });
      }else if(this.captcha==""){  // 验证码验证
        this.$toast({
          message: '验证码不能为空！',
          position: 'bottom',
          iconClass:'',
          duration: 3000
        });
      }else if(this.password==""){  // 密码非空验证
        this.$toast({
          message: '密码不能为空！',
          position: 'bottom',
          iconClass:'',
          duration: 3000
        });
      }else if(this.password!=this.repeatPwd){ // 两次密码一致性验证
        this.$toast({
          message: '两次密码输入不一致！',
          position: 'bottom',
          iconClass:'',
          duration: 3000
        });
      }else{
        this.regisAjax();
      }

    },
    register() {
      this.submitTest()

    }
  }
}
</script>
<style scoped>
.mint-header {
  background: none;
  color: #000;
}

.main {
  margin: 0 20px;
}

.input-group {
  border-bottom: 1px solid #000;
}

.button {
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  background: #000;
  color: #fff;
  margin-top: 10px;
}
</style>



