<template>
<div class="box">
  <div id="container">

  </div>
  <div id="r-result">

  </div>
</div>
</template>
<script>
export default {
  name: "Drving",
  data() {
    return {
    }
  },
  methods: {
    drving() {
      let _this = this
      let map = new BMap.Map("container");
      map.centerAndZoom(new BMap.Point(116.404, 39.915), 11);
      let geolocation = new BMap.Geolocation();
      // 开启SDK辅助定位
      // geolocation.enableSDKLocation();
      geolocation.getCurrentPosition(function(r) {
        if (this.getStatus() == BMAP_STATUS_SUCCESS) {
          // let mk = new BMap.Marker(r.point);
          let p1 = new BMap.Point(r.point.lng,r.point.lat);
          let p2 = new BMap.Point(116.508328, 39.919141);
          let driving = new BMap.DrivingRoute(map, {
            renderOptions: {
              map: map,
              panel: "r-result",
              autoViewport: true
            }
          });
          driving.search(p1, p2);
        } else {
          // alert('failed' + this.getStatus());
          MessageBox('温馨提示', '请在手机设置中开启位置');
        }
      });
    },
    addressDetail() { //获取地理位置
      let _this = this
      // alert("更新成功！！！")
      let map = new BMap.Map("container");
      let point = new BMap.Point(116.331398, 39.897445);
      map.centerAndZoom(point, 12);

    },

  },
  mounted() {
    this.drving()
  }
}
</script>
<style>
.box {
  padding-bottom: 50px;
}

#container {
  height: 300px;
  width: 100%;
}

#r-result,
#r-result table {
  width: 100%;
}
</style>



