<template>
<div class="">
  <div class="main">
    <div class="item" v-for="(item,index) in lists" :key="index" @click="toDetail(item.id)">
      <div class="item-top">
        <div class="pic">
          <img :src="item.image" />
        </div>
        <div class="txt">
          <h1 class="name">{{item.name}}</h1>
          <div class="desc">{{item.desc}}</div>
        </div>
      </div>
      <div class="content">
        {{item.content}}
      </div>
    </div>
  </div>
  <bottom-menu></bottom-menu>
</div>
</template>

<script>
import BottomMenu from '../menu/Menu.vue'
export default {
  name: "Coach",
  data() {
    return {
      lists: [{
          id: "1",
          name: "成吉思汗",
          image: require("../../assets/images/test1.jpg"),
          desc: "蒙古族第一大汗",
          content: "一代天骄成吉思汗，只识弯弓射大雕。 闪电发货哈代发货很大方的复核得分还得回放贷放"
        },
        {
          id: "2",
          name: "成吉思汗",
          image: require("../../assets/images/test2.jpg"),
          desc: "蒙古族第一大汗",
          content: "一代天骄成吉思汗，只识弯弓射大雕。 闪电发货哈代发货很大方的复核得分还得回放贷放"
        },
        {
          id: "3",
          name: "成吉思汗",
          image: require("../../assets/images/test3.jpg"),
          desc: "蒙古族第一大汗",
          content: "一代天骄成吉思汗，只识弯弓射大雕。 闪电发货哈代发货很大方的复核得分还得回放贷放"
        }
      ]
    }
  },
  methods:{
    toDetail(id){
      this.$router.push('/coach/detail/'+id)
    }
  },
  components: {
    BottomMenu
  },
  created(){
    this.$store.state.menuData="教练中心"
  }

}
</script>

<style scoped>
.main {
  padding-bottom: 50px;
}

.item {
  margin-bottom: 10px;
  background: #fff;
  padding: 10px;
}

.item-top {
  display: flex;
}

.pic {
  width: 60px;
  height: 60px;
  margin-right: 10px;

}

.pic img {
  width: 100%;
  height: 100%;
  display: block;
  border-radius: 50px;
}

.txt {
  flex: 1;
}

.name {
  margin: 5px 0;
}

.desc {
  font-size: 14px;
  color: #777
}

.content {
  padding: 10px 0;
  line-height: 22px;
}
</style>



