<template>
<div class="">
  <div class="main" v-if="show">
    <div style="text-align:center;">
      您的当前位置：<br />经度：{{curPosition.lng}}<br />纬度：{{curPosition.lat}}
    </div>
    <div class="item" v-for="(item,index) in lists" :key="index" @click="toDetail(item.id)">
      <div class="item-top">
        <div class="pic">
          <img :src="item.image" />
        </div>
        <div class="txt">
          <h1 class="name">{{item.name}}</h1>
          <div class="grade">
            {{item.grade}}分 <span class="rank">{{item.rank}}</span>
          </div>
          <div class="talk">
            {{item.talk}}条评论
          </div>
          <div class="distance">
            距离您{{item.distance}}公里
          </div>
        </div>
      </div>
    </div>
  </div>
  <bottom-menu></bottom-menu>
  <div id="allmap"></div>
  <div id="distance"></div>
</div>
</template>

<script>
import BottomMenu from '../menu/Menu.vue'
import {
  MessageBox
} from 'mint-ui';
export default {
  name: "Venue",
  data() {
    return {
      show: false,
      distance: 0,
      curPosition: {
        lng: 0,
        lat: 0
      },
      newLists: [],
      lists: [{
          id: "1",
          name: "大屯路东地铁站",
          image: require("../../assets/images/test1.jpg"),
          grade: 50,
          rank: "很差",
          talk: 3,
          position: {
            lng: 116.425226,
            lat: 40.009106
          }
        },
        {
          id: "2",
          name: "呼家楼地铁站",
          image: require("../../assets/images/test2.jpg"),
          grade: 70,
          rank: "一般",
          talk: 3,
          position: {
            lng: 116.468237,
            lat: 39.928981
          }
        },
        {
          id: "3",
          name: "四惠地铁站",
          image: require("../../assets/images/test3.jpg"),
          grade: 100,
          rank: "很棒",
          talk: 3,
          position: {
            lng: 116.501991,
            lat: 39.914743
          }
        }
      ]
    }
  },
  methods: {
    getDis(curPosition, targetPosition) { // 获取两点之间距离
      let map = new BMap.Map("distance");
      let curPoint = new BMap.Point(curPosition.lng, curPosition.lat);
      let tarPoint = new BMap.Point(targetPosition.lng, targetPosition.lat);
      let distance = map.getDistance(curPoint, tarPoint);
      distance = (distance / 1000).toFixed(1);
      return distance;
    },
    addressDetail() { //获取地理位置
      let _this = this
      // alert("更新成功！！！")
      var map = new BMap.Map("allmap");
      var point = new BMap.Point(116.331398, 39.897445);
      map.centerAndZoom(point, 12);

      var geolocation = new BMap.Geolocation();
      // 开启SDK辅助定位
      // geolocation.enableSDKLocation();
      geolocation.getCurrentPosition(function(r) {
        if (this.getStatus() == BMAP_STATUS_SUCCESS) {
          var mk = new BMap.Marker(r.point);
          map.addOverlay(mk);
          map.panTo(r.point);
          //alert('您的位置：' + r.point.lng + ',' + r.point.lat);
          _this.curPosition.lng = r.point.lng
          _this.curPosition.lat = r.point.lat
          _this.sortDistance()
        } else {
          // alert('failed' + this.getStatus());
          MessageBox('温馨提示', '请在手机设置中开启位置');
        }
      });
    },
    sortDistance() {
      let _this = this;
      this.lists.forEach(function(item, index) {
        _this.lists[index].distance = _this.getDis(_this.curPosition, item.position);
      })
      this.sortByKey(this.lists, "distance");
      this.show = true;
    },
    sortByKey(array, key) {
      return array.sort(function(a, b) {
        var x = a[key];
        var y = b[key];
        return x - y;
      })
    },
    getLocalStorage() {
      let ac_token = localStorage.getItem("ac_token");
      alert("ac_token:"+ac_token);
    },
    toDetail(id){
      this.$router.push(`/venue/detail/${id}`)
    }
  },
  components: {
    BottomMenu
  },
  created() {
    this.$store.state.menuData = "场馆中心"
  },
  mounted() {
    this.addressDetail();
    this.getLocalStorage()
  },

}
</script>

<style scoped>
#allmap {
  height: 350px;
  margin-bottom: 100px;
}

.main {
  padding-bottom: 50px;
}

.item {
  margin-bottom: 10px;
  background: #fff;
  padding: 10px;
}

.item-top {
  display: flex;
}

.pic {
  width: 60px;
  height: 60px;
  margin-right: 10px;

}

.pic img {
  width: 100%;
  height: 100%;
  display: block;
}

.txt {
  flex: 1;
}

.name {
  margin: 5px 0;
}

.desc {
  font-size: 14px;
  color: #777
}

.content {
  padding: 10px 0;
  line-height: 22px;
}
</style>



