<template>
<div class="main">
  <div class="pic">
    <img :src="coach.image" />
    <span class="pic-line"></span>
  </div>
  <div class="name">
    <span>{{coach.name}}</span>
    <i :class="coach.sex=='man'?'man':'woman'"></i>
  </div>
  <div class="grade">
    {{coach.grade}}分
  </div>
  <div class="">
    {{coach.pinglun}}人对{{coach.sex=="man"?"他":"她"}}评价
  </div>
  <div class="group">
    <div class="years">
      {{coach.years}}年
    </div>
    <div class="age">
      {{coach.age}}岁
    </div>
    <div class="project">
      {{coach.project}}
    </div>
  </div>
  <div class="coach-btn">
    <mt-button type="primary" @click="baoMing()">报名成为{{coach.sex=="man"?"他":"她"}}的学员</mt-button>
  </div>
</div>
</template>
<script>
import BottomMenu from '../../menu/Menu'
import {
  MessageBox,
  Toast
} from 'mint-ui';
export default {
  name: "Detail",
  data() {
    return {
      coach: {
        id: this.$route.params.id,
        name: "陈冠希",
        sex: "man",
        image: require("../../../assets/images/test1.jpg"),
        grade: 50,
        pinglun: 10,
        years: 10,
        age: 24,
        project: "手枪"
      }
    }
  },
  components: {
    BottomMenu
  },
  created() {
    this.$store.state.menuData = "",
      console.log(this.$route.params.id)
  },
  methods: {
    baoMing() {
      let sex = this.coach.sex == "man" ? "他" : "她"
      MessageBox.confirm('', {  
        message: '确定报名成为' + sex + '的学员吗？',
        title: '报名',
        confirmButtonText: '确定',
        cancelButtonText: '取消'  
      }).then(action => {  
        if (action == 'confirm') {   //确认的回调
          console.log(1);  
          Toast({
            message: '报名成功',
            iconClass: 'mint-toast-icon mintui mintui-success',
          });
        } 
      }).catch(err => {  
        console.log(err); 
      });

    }
  }
}
</script>
<style scoped>
.main {
  text-align: center;
}

.pic img {
  width: 50px;
  height: 50px;
  margin: 0 auto;
  display: block;
}

.name i {
  display: inline-block;
  width: 20px;
  height: 20px;
}

.name i.man {
  background: url("../../../assets/images/man.png");
  background-size: 100% 100%;
}

.name i.woman {
  background: url("../../../assets/images/woman.png");
  background-size: 100% 100%;
}

.coach-btn {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}

.coach-btn>>>button {
  width: 100%;
}
</style>



