import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home/Home'
import LoginStudent from '@/components/loginStudent/LoginStudent'
import Forget from '@/components/forget/Forget'
import ForgetNext from '@/components/forgetNext/ForgetNext'
import NewPassWord from '@/components/newPassWord/NewPassWord'
import Register from '@/components/register/Register'
import Main from '@/components/main/Main'
import MainDetail from '@/components/main/detail/Detail'
import Coach from '@/components/coach/Coach'
import CoachDetail from '@/components/coach/detail/Detail'
import Venue from '@/components/venue/Venue'
import VenueDetail from '@/components/venue/detail/Detail'
Vue.use(Router)
export default new Router({
  // mode:"history",
  routes: [
    {
      path: '/',
      name: 'Home',
      meta:{
        keepAlive:false
      },
      component: Home
    },
    {
      path: '/loginStudent',
      name: 'LoginStudent',
      meta:{
        keepAlive:true
      },
      component: LoginStudent
    },
    {
      path: '/forget',
      name: 'Forget',
      meta:{
        keepAlive:true
      },
      component: Forget
    },
    {
      path: '/forgetNext',
      name: 'ForgetNext',
      meta:{
        keepAlive:true
      },
      component: ForgetNext
    },
    {
      path: '/newPassWord',
      name: 'NewPassWord',
      meta:{
        keepAlive:true
      },
      component: NewPassWord
    },
    {
      path: '/register',
      name: 'Register',
      meta:{
        keepAlive:true
      },
      component: Register
    },
    {
      path: '/main',
      name: 'Main',
      meta:{
        keepAlive:false
      },
      component: Main
    },
    {
      path: '/main/detail/:id/',
      name: 'MainDetail',
      meta:{
        keepAlive:false
      },
      component: MainDetail
    },
    {
      path: '/coach',
      name: 'Coach',
      meta:{
        keepAlive:false
      },
      component: Coach
    },
    {
      path: '/coach/detail/:id/',
      name: 'CoachDetail',
      meta:{
        keepAlive:false
      },
      component: CoachDetail
    },
    {
      path: '/venue',
      name: 'Venue',
      meta:{
        keepAlive:false
      },
      component: Venue
    },
    {
      path: '/venue/detail/:id/',
      name: 'VenueDetail',
      meta:{
        keepAlive:false
      },
      component: VenueDetail
    }
  ]
})



