<template>
  <div class="swiper" :style="getHeight">
    <mt-swipe :auto="4000">
      <mt-swipe-item v-for="(item,index) in lists" :key="index"><img :src="item"/></mt-swipe-item>
    </mt-swipe>
  </div>
</template>

<script>
export default {
  name:"Swiper",
  data (){
    return {
      lists:[
        require("../../../assets/images/test1.jpg"),
        require("../../../assets/images/test2.jpg"),
        require("../../../assets/images/test3.jpg")
      ]
    }
  },
  computed:{
    getHeight(){
      let _w=document.body.clientWidth
      return "height:"+_w*9/16+"px"
    }
  }
}
</script>

<style scoped>
.swiper{
  height: 50px;
}
.swiper img{
  display: block;
  width: 100%;
  height: 100%;
}
</style>



