<template>
<div class="">
  <div class="detail-panel">
    <div class="detail-group">
      <span class="name">比赛名称</span>
      <span class="content">设计比赛热门项目</span>
    </div>
    <div class="detail-group">
      <span class="name">比赛时间</span>
      <span class="content">2019-3-29至2019-6-24</span>
    </div>
    <div class="detail-group">
      <span class="name">比赛项目</span>
      <span class="content">狙击步枪</span>
    </div>
    <div class="detail-group">
      <span class="name">比赛分组</span>
      <span class="content">少年组</span>
    </div>
    <div class="detail-group">
      <span class="name">比赛场馆</span>
      <span class="content">蓝光云鼎</span>
    </div>
    <div class="detail-group">
      <span class="name">参与人数</span>
      <span class="content">22</span>
    </div>
  </div>
  <div class="detail-desc">
    比赛介绍
  </div>
  <div class="detail-title">
    的几点几分打飞机交大附近的减肥大幅度发大幅度发得分王二翁翁热热尔额无热热若
  </div>
  <div class="detail-buttons">
    <p>
      <mt-button type="default" @click="show=true">分享</mt-button>
    </p>
    <p>
      <mt-button type="primary">评论</mt-button>
    </p>
    <p>
      <mt-button type="danger" @click="apply()">报名比赛</mt-button>
    </p>
  </div>
  <div class="share-txt" @click="show=false" v-if="show">
    <img src="@/assets/images/tishi.png" />
  </div>
  <bottom-menu></bottom-menu>
</div>
</template>
<script>
import BottomMenu from '../../menu/Menu'
import {Toast,MessageBox} from 'mint-ui';
export default {
  name: "Detail",
  data() {
    return {
      show: false
    }
  },
  components: {
    BottomMenu
  },
  created() {
    this.$store.state.menuData = "",
      console.log(this.$route.params.id)
  },
  methods: {
    apply() {
      MessageBox.confirm('', {  
        message: '确定报名吗？',
          title: '报名',
          confirmButtonText: '确定',
          cancelButtonText: '取消'  
      }).then(action => {  
        if (action == 'confirm') {   //确认的回调
          console.log(1);  
          Toast({
            message: '报名成功',
            iconClass: 'mint-toast-icon mintui mintui-success',
          });
        } 
      }).catch(err => {  
        console.log(err); 
      });
    }
  }
}
</script>
<style>
.detail-panel {
  margin: 30px 10px;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 1px 1px 10px 1px #ccc;
}

.detail-group {
  border-bottom: 1px solid #000;
  padding: 5px 0;
  overflow: hidden;
}

.detail-group .name {
  float: left;
}

.detail-group .content {
  float: right;
}

.detail-buttons a {
  display: block;
  width: 100%;
  height: 40px;
}

.share-txt {
  background: rgba(0, 0, 0, .7);
  position: fixed;
  left: 0;
  top: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  text-align: center;
}

.share-txt img {
  margin-top: 30px;
}
</style>



