<template>
<div class="">
  <div class="mtnavbar">
    <mt-navbar v-model="barnum">
      <mt-tab-item id="1">驾车</mt-tab-item>
      <mt-tab-item id="2">公交</mt-tab-item>
      <mt-tab-item id="3">步行</mt-tab-item>
    </mt-navbar>
  </div>
  <keep-alive>
    <map-drving :is="selected"></map-drving>
  </keep-alive>
  <bottom-menu></bottom-menu>
</div>
</template>
<script>
import BottomMenu from '../../menu/Menu'
import MapDrving from './drving/Drving'
import MapTransit from './transit/Transit'
import MapWalking from './walking/Walking'
import {
  Toast,
  MessageBox
} from 'mint-ui';
export default {
  name: "Detail",
  data() {
    return {
      show: false,
      barnum:"1",
      selected: "MapDrving"
    }
  },
  components: {
    BottomMenu,
    MapDrving,
    MapTransit,
    MapWalking,
  },
  watch:{
    barnum(val){
      if(val==1){
        this.selected="MapDrving"
      }else if(val==2){
        this.selected="MapTransit"
      }else if(val==3){
        this.selected="MapWalking"
      }
    }
  }
}
</script>
<style scoped>
.mtnavbar{
  margin-bottom: 3px
}
</style>



